.meanings{
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  scrollbar-width:thin;
  height: 55vh;
  padding: 10px 25px;
  overflow-x: hidden;
}
.subTitle{
  font-size: 3vw;
  font-family: 'Roboto', sans-serif;

}


@media(max-width:900px){
.meanings{
  height: 60vh;
  overflow-x: hidden;
  overflow-y: scroll;
}
}
.singleMean{
  display: flex;
  flex-direction: column;
  border-radius: 10px ;
  padding: 10px 20px;
  margin:10px 0;
}
