
.title{
  font-size: 7vw;
  text-transform: uppercase;
  font-family: 'Roboto', sans-serif;
}
.header{
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  height: 35vh;
  width: 100%;
}
 .inputs{
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.search,.select{
  width: 43%;

}
/* if the width is lesser than 900px */
@media(max-width:900px){
.header{
  
  height:25vh;
}
.title{
  font-size:11vw;

}
.inputs{
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.search,.select{
  width: 43%;

}
}