@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body{
  font-family: 'Lato', sans-serif;
  margin:0;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background:rgb(105,105,105);
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.title{
  font-size: 7vw;
  text-transform: uppercase;
  font-family: 'Roboto', sans-serif;
}
.header{
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  height: 35vh;
  width: 100%;
}
 .inputs{
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.search,.select{
  width: 43%;

}
/* if the width is lesser than 900px */
@media(max-width:900px){
.header{
  
  height:25vh;
}
.title{
  font-size:11vw;

}
.inputs{
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.search,.select{
  width: 43%;

}
}
.meanings{
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  scrollbar-width:thin;
  height: 55vh;
  padding: 10px 25px;
  overflow-x: hidden;
}
.subTitle{
  font-size: 3vw;
  font-family: 'Roboto', sans-serif;

}


@media(max-width:900px){
.meanings{
  height: 60vh;
  overflow-x: hidden;
  overflow-y: scroll;
}
}
.singleMean{
  display: flex;
  flex-direction: column;
  border-radius: 10px ;
  padding: 10px 20px;
  margin:10px 0;
}

